import React, { useEffect } from 'react';
import "../assets/css/App.css";

const Event = () => {
  const showModal = () => {
    const modal = new window.bootstrap.Modal(document.getElementById('exampleModal'));
    modal.show();
  };

  useEffect(() => {
    showModal();  
  }, []); 

  return (
    <>
  <div
  className="modal fade"
  id="exampleModal"
  tabIndex="-1"
  aria-labelledby="exampleModalLabel"
  aria-hidden="true"
>
  <div className="modal-dialog modal-lg modal-dialog-centered">
    <div className="modal-content  bg-white bg-body-change ">
      {/* Modal Header */}
      <div className="d-flex justify-content-end">
        <button
          type="button"
          className="btn-close changed-close"
          data-bs-dismiss="modal"
          aria-label="Close" 
        ></button>
      </div>

      {/* Modal Body */}
      <div className="modal-body changed-pad">
        <img
          src="/assets/images/Novotel-Event.jpg"
          className="w-100 border-radius-change"
          alt="Novotel Event"
        />
        <div className="d-flex gap-2 justify-content-end">
        <button className="border-none mt-2 mb-2 w-100">
          <a
            href="https://events.bizdateup.com/"
            target="_blank"
            className="text-part text-decoration-none fw-medium"
          >
            Register Now
          </a>
        </button>
       
        </div>
      </div>
    </div>
  </div>
</div>

    </>
  );
};

export default Event;